import { useState } from 'react'
import { toast } from 'react-toastify';

export default function DeleteModal({ onClose, recordId, reload }) {

    const [loading, setLoading] = useState(false);

    const deleteHandler = async () => {
        try {

            setLoading(true);
            toast.info("  ...");

            const response = await fetch(
                `https://api.backendless.com/F709728E-F527-4D4C-B3DA-C415F4581F77/D270E61A-C6A4-4589-88B4-4AF48BF29ABB/data/glucoseData/${recordId}`,
                {
                    method: "DELETE",
                    headers: {
                        "Content-Type": "application/json",
                    },
                }
            );

            if (!response.ok) {
                const errText = await response.text();
                console.error("  :", errText);
                return;
            }

            const data = await response.json();
            toast.success("    !");
            setLoading(false);
            reload();
            onClose();
        } catch (error) {
            console.error("   :", error);
            toast.error("   !");
            setLoading(false);
        }
    };

    return (
        <div className='modal'>
            <div className='modal__content'>
                <h2 className='modal__title'> </h2>
                <p className='modal__message'>       <br />    </p>
                <div className='modal__buttons'>
                    <button
                        className='modal__button modal__button--confirm'
                        onClick={deleteHandler}
                        disabled={loading}
                    ></button>
                    <button
                        className='modal__button modal__button--cancel'
                        onClick={onClose}
                        disabled={loading}
                    ></button>
                </div>
            </div>
        </div>
    )
}
